<template>
	<div class="ranking">
		<h3 class="ranking-head" >男生</h3>
		<div v-for="(p, index) in rankUrl.slice(0,7)" :key="index">
			<router-link :to="/ranklist/ + p.title + '/' +  p._id ">
				<div class="ranking-body" >
					<img :src="[link + p.cover]" class="ranking-img" >
					<span>{{p.title}}</span>
				</div>
			</router-link>
		</div>
		<div class="ranking-body" @click="OtherArr()">
			<img src="../../public/img/other.png" class="ranking-img">
			<span>别人家的排行榜</span>
			<img src="../../public/img/arror.png" class="rank-other auto-img">
		</div>
		<div>
			<div v-for="(arr, other) in rankUrl.slice(7)" :key="other" v-show="arror">
				<router-link :to="/ranklist/ + arr.title + '/' +  arr._id ">
					<div class="ranking-body" >
						<img :src="[link + arr.cover]" class="ranking-img" >
						<span>{{arr.title}}</span>
					</div>
				</router-link>
			</div>
		</div>
		
		
		<div>
			<h3 class="ranking-head" >女生</h3>
			<div v-for="(g, indexs) in rankUrls.slice(0,7)" :key="indexs">
				<router-link :to="/ranklist/ + g.title + '/' +  g._id ">
				<div class="ranking-body" >
					<img :src="[link + g.cover]" class="ranking-img" >
					<span>{{g.title}}</span>
				</div>
				</router-link>
			</div>
		</div>
		<div class="ranking-body" @click="OtherArr()">
			<img src="../../public/img/other.png" class="ranking-img">
			<span>别人家的排行榜</span>
			<img src="../../public/img/arror.png" class="rank-other auto-img">
		</div>
		<div>
			<div v-for="(arrs, others) in rankUrls.slice(7)" :key="others" v-show="arror">
				<router-link :to="/ranklist/ + arrs.title + '/' +  arrs._id ">
					<div class="ranking-body" >
						<img :src="[link + arrs.cover]" class="ranking-img" >
						<span>{{arrs.title}}</span>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	const url = "api/ranking/gender"
	
	export default {
		name: "Rank",
		data(){
			return{
				rankUrl:[],
				rankUrls:[],
				link:'http://statics.zhuishushenqi.com',
				arror:false
			}
		},
		created() {
			this.axios.get(url).then(p =>{
				this.rankUrl = p.data.male
				this.rankUrls = p.data.female
			})
		},
		methods: {
			OtherArr() {
				this.arror = !this.arror
			}
		},
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	
	.ranking-head{
		padding: 23px 15px 15px; 
		color: #999;
		font-size: 10px;
		
		
	}
	
	.ranking-body{
		width: 100%;
		height: 3.75rem;
		background-color: #fff;
		border-bottom: 1px solid #ebebeb;
		position: relative;
		
		.ranking-img{
			width: 1.875rem;
			height: 1.875rem;
			margin: 0 10px;
			line-height: 1.875rem;
			vertical-align: middle;
		}
		
		span{
			width: 100%;
			height: 3.75rem;
			text-align: center;
			line-height: 3.75rem;
			color: #333;
			font-size: 14px;
			font-weight: 500;
		}
		
		.rank-other{
			float: right;
			position: absolute;
			right: 0;
			top: 0;
			margin: 1.375rem;
			transform: rotate(-90deg);
			width: 0.875rem;
			height: 0.875rem;
		}
	}
	
	
</style>
